.ticker-preview {
    background-image: url('../../assets/post_bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    width: 50%;
    margin: 0 auto;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;

    .user-profile {
        margin-top: 10px;
        display: flex;

        img {
            width: 60px;

        }

        h1 {
            margin-top: 0px;
        }
    }
}